import React from 'react';
import {
  IconButton,
  SvgIcon,
  Tooltip,
  Zoom,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  TextField,
  ButtonBase,
  Box,
  Typography,
} from '@material-ui/core';
import { useTheme, makeStyles } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';

import { useStore } from '../../hooks/store';
import {
  folderAccountIcon,
  folderAddIcon,
  plusIcon,
  backIcon,
} from '../../assets/svg/Common';
import CardMoreOption from './CardMoreOption';
import CardHolder from '../Account/CardHolder';

const useStyles = makeStyles((theme) => ({
  svgContainer: {
    width: theme.spacing(15),
    height: theme.spacing(15),
    color: theme.palette.mode === 'dark' ? grey[300] : grey[700],
  },
  buttonContainer: { borderRadius: theme.spacing(0.5) },
}));

const DialogInnerContent = (props) => {
  const {
    contentMode,
    isCreationMode,
    toggleModeHandler,
    addHandler,
    contentType,
    contentData,
  } = props;
  const theme = useTheme();
  const classes = useStyles();
  const [state, dispatch] = useStore();

  let content;

  if (contentType === 'cardHolder') {
    content = state.user.cardHolders;
  } else if (contentType === 'cardOptions') {
    content = contentData;
  }

  return isCreationMode ? (
    <Box sx={{ display: 'flex' }}>
      <Box sx={{ alignSelf: 'center', width: 48, height: 48 }}>
        <Tooltip arrow TransitionComponent={Zoom} title="Back">
          <IconButton aria-label="back" onClick={toggleModeHandler}>
            <SvgIcon>
              <path d={backIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Box>
      <TextField
        autoFocus
        margin="dense"
        id="folder-name"
        label="Folder Name"
        type="text"
        fullWidth
        variant="standard"
        color="secondary"
      />
    </Box>
  ) : contentMode === 'folderList' ? (
    <List>
      {content.map((folder) => (
        <ListItem button key={folder.label} onClick={addHandler}>
          <ListItemIcon>
            <SvgIcon>
              <path d={folderAccountIcon.path} />
            </SvgIcon>
          </ListItemIcon>
          <ListItemText primary={folder.label} />
        </ListItem>
      ))}
      <ListItem button onClick={toggleModeHandler}>
        <ListItemIcon>
          <SvgIcon>
            <path d={folderAddIcon.path} />
          </SvgIcon>
        </ListItemIcon>
        <ListItemText primary="New Card Holder" />
      </ListItem>
    </List>
  ) : contentMode === 'actionList' ? (
    <List>
      {content.map((action) => (
        <CardMoreOption key={action.id} linkItem={action} />
      ))}
    </List>
  ) : contentMode === 'folder' ? (
    <Box
      sx={{
        display: 'grid',
        [theme.breakpoints.down('xs')]: {
          gridTemplateColumns: `120px 120px`,
        },
        [theme.breakpoints.up('xs')]: {
          gridTemplateColumns: `120px 120px 120px`,
        },

        justifyContent: 'center',
        gridGap: `${theme.spacing(2)}`,
      }}
    >
      {content.map((cardHolder) => (
        <CardHolder
          key={cardHolder.label}
          cardHolder={cardHolder}
          bookmarkHandler={addHandler}
        />
      ))}
      <Tooltip arrow TransitionComponent={Zoom} title="New Card Holder">
        <ButtonBase
          focusRipple={true}
          onClick={toggleModeHandler}
          className={classes.buttonContainer}
        >
          <Box sx={{ display: 'grid' }}>
            <SvgIcon className={classes.svgContainer}>
              <path d={folderAddIcon.path} />
            </SvgIcon>
            <Typography variant="caption" noWrap={true} gutterBottom={true}>
              New Card Holder
            </Typography>
          </Box>
        </ButtonBase>
      </Tooltip>
    </Box>
  ) : null;
};

export default DialogInnerContent;
