import React from 'react';
import {
  Dialog,
  DialogActions,
  DialogContent,
  SvgIcon,
  Button,
  useMediaQuery,
  SwipeableDrawer,
  Typography,
  TextField,
  Box,
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import { LoadingButton } from '@material-ui/lab';
import { Link as RouterLink } from 'react-router-dom';
import QRCode from 'react-qr-code';

import {
  folderAccountIcon,
  folderAddIcon,
  plusIcon,
} from '../../assets/svg/Common';
import CustomDialogTitle from './CustomDialogTitle';
import { useStore } from '../../hooks/store';
import DialogInnerContent from './DialogInnerContent';

const ResponsiveDialog = (props) => {
  const {
    isOpen,
    closeHandler,
    svgPath,
    isCreationMode,
    toggleModeHandler,
    contentMode,
    addHandler,
    contentType,
    contentData,
    children,
  } = props;
  const [state, dispatch] = useStore();
  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));
  const isNotSmallDevice = useMediaQuery(theme.breakpoints.up('sm'));

  const Content = () => {
    return (
      <>
        <CustomDialogTitle
          id="bookmark-dialog-title"
          onClose={closeHandler}
          svgIcon={svgPath}
          // isCreationMode={isCreationMode}
          // toggleModeHandler={toggleModeHandler}
          // onAdd={bookmarkAddHandler}
        >
          {children}
        </CustomDialogTitle>
        <DialogContent dividers>
          {contentType === 'qrCode' ? (
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'center',
              }}
            >
              <QRCode value={contentData} />
            </Box>
          ) : state.isAuthenticated ? (
            <DialogInnerContent
              contentMode={contentMode}
              isCreationMode={isCreationMode}
              toggleModeHandler={toggleModeHandler}
              addHandler={addHandler}
              contentType={contentType}
              contentData={contentData}
            />
          ) : (
            <Typography>
              Please{' '}
              <Button
                component={RouterLink}
                to={`/signin`}
                color="secondary"
                variant="contained"
                onClick={addHandler}
              >
                Sign In
              </Button>{' '}
              in order to continue.
            </Typography>
          )}
        </DialogContent>

        {isCreationMode ? (
          <DialogActions>
            <LoadingButton
              pendingPosition="start"
              color="secondary"
              startIcon={
                <SvgIcon>
                  <path d={plusIcon.path} />
                </SvgIcon>
              }
              onClick={addHandler}
            >
              Create
            </LoadingButton>
          </DialogActions>
        ) : null}
      </>
    );
  };

  return isSmallDevice ? (
    <SwipeableDrawer
      anchor="bottom"
      onOpen={closeHandler}
      open={isOpen}
      onClose={closeHandler}
      disableDiscovery={true}
      disableSwipeToOpen={true}
    >
      <Content />
    </SwipeableDrawer>
  ) : (
    <Dialog
      open={isOpen}
      aria-labelledby="bookmark-dialog-title"
      onClose={closeHandler}
      fullWidth
      maxWidth="xs"
    >
      <Content />
    </Dialog>
  );
};

export default ResponsiveDialog;
