import React from 'react';
import { IconButton, Link, SvgIcon, Tooltip, Zoom } from '@material-ui/core';
import {
  phoneIcon,
  messageIcon,
  emailIcon,
  qrCodeIcon,
  chevronDownIcon,
  bookmarkIcon,
  bookmarkMultipleIcon,
} from '../../assets/svg/Common';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import ResponsiveDialog from './ResponsiveDialog';

import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
}));

const CardActionLinks = (props) => {
  const {
    data,
    bookmarkIsOpen,
    shareIsOpen,
    qrCodeIsOpen,
    bookmarkHandler,
    shareHandler,
    qrCodeHandler,
    expanded,
    handleExpandClick,
    bookmarkList,
    isCreationMode,
    creationModeHandler,
    contentMode,
    addHandler,
  } = props;
  const classes = useStyles();

  const theme = useTheme();

  return (
    <>
      {/* <Link
        href={data.website}
        underline="none"
        target="_blank"
        rel="noopener"
        component="a"
      >
        <IconButton aria-label="website">
          <SvgIcon>
            <path d={earthIcon.path} />
          </SvgIcon>
        </IconButton>
      </Link> */}
      <Tooltip arrow TransitionComponent={Zoom} title="Bookmark">
        <IconButton aria-label="bookmark" onClick={bookmarkHandler}>
          <SvgIcon>
            <path d={bookmarkIcon.path} />
          </SvgIcon>
        </IconButton>
      </Tooltip>

      <ResponsiveDialog
        isOpen={bookmarkIsOpen}
        closeHandler={bookmarkHandler}
        svgPath={bookmarkMultipleIcon.path}
        isCreationMode={isCreationMode}
        toggleModeHandler={creationModeHandler}
        contentMode={'folder'}
        addHandler={addHandler}
        contentType={'cardHolder'}
        contentData={''}
      >
        Card Holders
      </ResponsiveDialog>

      <Link
        href={`tel:+${data.contactPerson.phone}`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Call">
          <IconButton aria-label="call">
            <SvgIcon>
              <path d={phoneIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      <Link
        href={`mailto:${data.contactPerson.email}`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Email">
          <IconButton aria-label="email">
            <SvgIcon>
              <path d={emailIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      <Link
        href={`sms:/${data.contactPerson.phone}/?body=/${data.contactPerson.sms}/`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Message">
          <IconButton aria-label="message">
            <SvgIcon>
              <path d={messageIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      <Tooltip arrow TransitionComponent={Zoom} title="QR Code">
        <IconButton aria-label="qr-code" onClick={qrCodeHandler}>
          <SvgIcon>
            <path d={qrCodeIcon.path} />
          </SvgIcon>
        </IconButton>
      </Tooltip>

      <ResponsiveDialog
        isOpen={qrCodeIsOpen}
        closeHandler={qrCodeHandler}
        svgPath={qrCodeIcon.path}
        isCreationMode={isCreationMode}
        toggleModeHandler={creationModeHandler}
        contentMode={'content'}
        addHandler={addHandler}
        contentType={'qrCode'}
        contentData={data.initLink}
      >
        QR Code
      </ResponsiveDialog>
      <IconButton
        className={clsx(classes.expand, {
          [classes.expandOpen]: expanded,
        })}
        onClick={handleExpandClick}
        aria-expanded={expanded}
        aria-label="show more"
      >
        <SvgIcon>
          <path d={chevronDownIcon.path} />
        </SvgIcon>
      </IconButton>
    </>
  );
};

export default CardActionLinks;
